const container = document.querySelector(".gallery");

function createCardMarkup({ preview, original, description }) {
  const markup = `<li class="gallery-item">
            <a class="gallery-link" href="${original}">
                <img class="gallery-image"
                src="${preview}"
                data-source="${original}"
                alt="${description}"/>
            </a>
        </li>`;
  return markup;
}

let markup = "";
for (let image of images) {
  markup += createCardMarkup(image);
}

// клік на пусте поле
container.innerHTML = markup;
container.addEventListener("click", (event) => {
  if (event.target === event.currentTarget) {
    return;
  }
  {
    console.log(event.target.dataset.source);
  }
  const dataSource = event.target.getAttribute("data-source");
  const description = event.target.getAttribute(`alt`);
  basicLightbox.create(
      `<div class="modal">
    <img src="${dataSource}"
    alt=""
    width="1280" height="900"/>
</div>`,
    ).show();
});

// зупиняє загрузку
container.addEventListener("click", (event) => {
  event.preventDefault();
});
